<template>
  <div>
    <div class="m-2 text-2xl">Profile</div>
    <div class="m-2">{{ $store.getters.userName }}</div>
    <div class="m-2">{{ $store.getLoginToken() }}</div>
    <div>{{ getUID() }}</div>
    <div>
      <button
        class="m-2 py-1 px-2 rounded-lg bg-red-600 text-white"
        @click="onLogout()"
      >
        로그아웃
      </button>
    </div>
  </div>
</template>
<script>
export default {
  mounted() {
    if (!this.$store.getters.isLogin) {
      this.$router.push("/login");
    }
  },
  methods: {
    onLogout() {
      this.$store.commit("clear", {});
      this.$router.push("/");
    },
    getUID() {
      return MediaDeviceInfo.deviceId;
    },
  },
};
</script>
